import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";
import AddPartnerDailogBox from "./AddPartnerDailogBox";

export function AddPartnerBtn() {
  return (
    <Stack spacing={2} direction="row">
      <Button onClick={()=>{
        <AddPartnerDailogBox/>
      }} variant="contained">Add Partner</Button>
    </Stack>
  );
}

